function format(num) {
    // 0
    return num.substring(num.length - 2, num.length);
}
function returnDate(time, type) {
    if (!time) return '';
    const date = new Date(time);
    const y = 1900 + date.getYear(); // 
    const m = `0${date.getMonth() + 1}`; // 
    const d = `0${date.getDate()}`; // 
    const h = `0${date.getHours()}`; // 
    const min = `0${date.getMinutes()}`; // 
    const s = `0${date.getSeconds()}`; // 
    const date1 = `${y}-${format(m)}-${format(d)}`; // 
    const date2 = `${format(h)}:${format(min)}:${format(s)}`; // 
    // hour
    const date3 = type === 'hour' ? `${date1} ${date2}` : date1;
    return date3;
}

export default {
    returnDate
};
