<template>
    <div class="home">
        <div class="header">
            {{type === 'add' ? '' : ''}}
            <div class="back-btn">
                <el-button v-show="isEditAndDeleteBtnShow"
                           @click="deleteTag()"
                           size="mini"
                           type="danger"></el-button>
                <el-button v-show="isEditAndDeleteBtnShow"
                           @click="editBtnClick()"
                           type="warning"
                           size="mini"></el-button>
                <el-button v-show="isEdit && type === 'look' && params.labelType === '1'"
                           @click="save()"
                           size="mini"></el-button>
                <el-button v-show="type === 'add'"
                           @click="save()"
                           size="mini"
                           type="success"></el-button>
                <el-button @click="back()"
                           size="mini"></el-button>
            </div>
        </div>
        <div class="content">
            <div class="item">
                <div class="item-title">
                    <span></span>
                </div>
                <div class="item-content">
                    <div class="list"
                         v-for="(item, index1) in showList"
                         :key="index1">
                        <span class="list-name">{{item.name}}</span>
                        <el-select v-model="params[item.key]"
                                   :disabled="isDisabled(item)"
                                   size="small"
                                   @change="selectChange(item)">
                            <el-option v-for="(itemSelect, index2) in item.list"
                                       :key="index2"
                                       :label="itemSelect.name || itemSelect.labelGroupName"
                                       :value="itemSelect.value || itemSelect.labelGroupId">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="list">
                        <span class="list-name"></span>
                        <div class="input-box">
                            <el-input v-model="params.labelName"
                                      :disabled="isShow2"
                                      maxlength="30"
                                      size="small"></el-input>
                            <p class="explain"
                               v-if="type === 'add'">305</p>
                        </div>
                    </div>
                    <div class="list">
                        <span class="list-name"></span>
                        <div class="input-box">
                            <el-input type="textarea"
                                      :autosize="{ minRows: 2, maxRows: 4}"
                                      placeholder=""
                                      :disabled="isShow2"
                                      resize="none"
                                      maxlength="100"
                                      v-model="params.remark">
                            </el-input>
                            <p class="explain"
                               v-if="type === 'add'">100</p>
                        </div>
                    </div>
                    <div class="line"></div>
                    <div class="list"
                         v-if="type === 'look'">
                        <span class="list-name"></span>
                        {{createTime || ''}}
                    </div>
                    <div class="list"
                         v-if="type === 'look'">
                        <span class="list-name"></span>
                        {{lastUpdateTime || ''}}
                    </div>
                    <div class="list">
                        <span class="list-name"></span>
                        {{labelStatus || ''}}
                    </div>
                </div>
            </div>
            <div class="item"
                 v-if="type === 'look'">
                <div class="item-title">
                    <span></span>
                    <span class="font-blue"> {{categoryNum}}  {{totalNum}} </span>
                    <div class="back-btn"
                         v-if="params.labelType === '1' && type === 'look' && params.labelStatus === '1'">
                        <el-button @click="addRelation()"
                                   size="mini"></el-button>
                    </div>
                </div>
                <div class="item-content">
                    <div class="list">
                        <span class="list-name"></span>
                        {{userRelationNum}}
                    </div>
                    <div class="list">
                        <span class="list-name"></span>
                        {{ingredientsRelationNum}}
                    </div>
                    <div class="list">
                        <span class="list-name"></span>
                        {{marketingRelationNum}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import {
    tagList
} from '@/utils/tagsSearch';
import timeHandle from '../../utils/timeHandle.js';

// tagListimport tagListsplice(5, 2)tagList
const showList = JSON.parse(JSON.stringify(tagList));

export default {
    name: 'tagDetail',
    props: ['id'],
    data() {
        return {
            showList, // 
            type: 'add', // /
            isEdit: false, // 
            params: {
                labelStatus: '',
                remark: '',
                labelName: '',
                labelPartition: '',
                labelGroupId: '',
                labelType: '',
                isUserShow: ''
            }, // 
            createTime: '', // 
            lastUpdateTime: '', // 
            userRelationNum: 0, // 
            ingredientsRelationNum: 0, // 
            marketingRelationNum: 0, // 
            categoryNum: 0, // 
            totalNum: 0 // 
        };
    },
    computed: {
        isEditAndDeleteBtnShow() {
            return !this.isEdit && this.type === 'look' && this.params.labelType === '1' && this.params.labelStatus === '2';
        },
        isShow2() {
            return (!this.isEdit && this.type === 'look') || this.params.labelType === '2';
        },
        labelStatus() {
            const labelStatusMap = {
                1: '',
                2: ''
            };
            return labelStatusMap[this.params.labelStatus] || '';
        }
    },
    created() {
        this.showList.splice(4, 3);
        this.showList.forEach((item) => {
            if (item.list.length) item.list[0].name = '';
            this.$set(this.params, item.key, '0');
        });
        this.getTagGroupList();
        this.params.labelId = this.$route.query.id || '';
        this.type = this.params.labelId ? 'look' : 'add';
        // 
        if (!this.params.labelId) {
            this.params.labelType = '1';
            this.params.labelStatus = '1';
        }
        if (this.params.labelId) this.getDetail();
    },
    methods: {
        returnDate(time) {
            return timeHandle.returnDate(time, 'hour');
        },
        isDisabled(item) {
            return (item.name === '') // 
            || (this.type === 'look' && !this.isEdit) // 
            || ((this.type === 'look' && this.isEdit) && (item.name === '' || item.name === '')) // 
            || this.params.labelType === '2' // 
            || (this.params.labelPartition === '1' && item.name === ''); // 
        },
        selectChange(item) {
            if (item.name === '' && this.params.labelPartition === '1') this.params.labelGroupId = '0';
        },
        back() {
            if (this.isEdit) {
                this.isEdit = false;
                this.getDetail();
            } else {
                this.$router.push('/tag/list');
            }
        },
        getDetail() {
            this.$loading('...');
            this.$get('/crm/label/detail.json', { labelId: this.params.labelId }).then((res) => {
                const data = res.data.object;
                // 
                // null'null'
                // selectionvalue
                const fields = ['labelPartition', 'labelGroupId', 'isUserShow', 'labelStatus', 'labelType'];
                fields.forEach((item) => {
                    if (data[item] !== null) {
                        data[item] = `${data[item]}`;
                    }
                    this.params[item] = data[item];
                });
                // this.params.labelPartition = data.labelPartition && `${data.labelPartition}`;
                // this.params.labelGroupId = data.labelGroupId && `${data.labelGroupId}`;
                // this.params.isUserShow = data.isUserShow && `${data.isUserShow}`;
                // this.params.labelStatus = data.labelStatus && `${data.labelStatus}`;
                // this.params.labelType = data.labelType && `${data.labelType}`;
                this.params.labelName = data.labelName;
                this.createTime = this.returnDate(data.createTime);
                this.lastUpdateTime = this.returnDate(data.lastUpdateTime);
                this.params.remark = data.remark;
                this.userRelationNum = data.userRelationNum;
                this.ingredientsRelationNum = data.ingredientsRelationNum;
                this.marketingRelationNum = data.marketingRelationNum;
                this.categoryNum = this.categoryNum + (this.userRelationNum ? 1 : 0) + (this.ingredientsRelationNum ? 1 : 0) + (this.marketingRelationNum ? 1 : 0);
                this.totalNum = this.userRelationNum + this.ingredientsRelationNum + this.marketingRelationNum;
            }).finally(() => {
                this.$loading().close();
            });
        },
        deleteTag() {
            this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$loading('...');
                this.$get('/crm/label/delete.json', { labelIds: this.params.labelId }).then(() => {
                    this.$message.success('');
                    this.$router.push('/tag/list');
                }).cath(error => console.log(error)).finally(() => {
                    this.$loading().close();
                });
            }).catch(error => console.log(error));
        },
        save() {
            const msg = this.checkData();
            if (msg) return this.$message.error(msg);
            this.$loading('...');
            this.$post('/crm/label/addLabel.json', this.params).then(((res) => {
                this.$message.success('');
                if (res && res.data && res.data.object) {
                    const { labelId } = res.data.object;
                    this.$router.replace(`/tag/detail?id=${labelId}`);
                    this.type = 'look';
                    this.params.labelId = labelId;
                    this.isEdit = false;
                    this.getDetail();
                }
            })).catch(error => console.log(error)).finally(() => {
                this.$loading().close();
            });
            return '';
        },
        checkData() {
            let msg = '';
            if (!this.params.remark) msg = '';
            if (!this.params.labelName) msg = '';
            if (this.params.isUserShow === '0') msg = '';
            if (this.params.labelGroupId === '0' && this.params.labelPartition !== '1') msg = '';
            if (this.params.labelPartition === '0') msg = '';
            return msg;
        },
        addRelation() {
            this.$router.push(`/addRelation?id=${this.params.labelId}&type=${this.params.labelPartition}`);
        },
        getTagGroupList() {
            this.$get('/crm/label/group/list.json').then((res) => {
                let array = res.data.object;
                if (array instanceof Array) {
                    array = array.map(item => ({ ...item, labelGroupId: `${item.labelGroupId}` }));
                }
                this.showList[1].list = array;
                // 
                this.showList[1].list.unshift({
                    labelGroupId: '0',
                    labelGroupName: ''
                });
            }).catch(err => console.log(err));
        },
        editBtnClick() {
            this.isEdit = true;
            // 0
            // if (this.params.labelType === '1' && this.params.labelStatus === '1' && this.totalNum > 0) {
            //     this.$message.error('');
            // } else {
            //     this.isEdit = true;
            // }
        }
    }
};
</script>

