<template>
    <div class="container">
        <div class="header">{{title}} <el-button class="back-Btn" size="small" type="primary" icon="el-icon-back" @click="back"></el-button> </div>
        <el-form label-position="left" label-width="80px" :model="form">
            <el-form-item v-if="edit" required label="">
                <el-upload
                        action="/uploadImage.html?action=uploadImage"
                        :file-list="this.form.pictureList"
                        :limit="1"
                        :before-upload="beforeUpload"
                        :on-success="uploadSuccess"
                        :on-error="uploadError"
                        :disabled="Boolean(edit && this.form.pictureList[0])"
                        :show-file-list="false"
                        list-type="picture">
                    <el-button size="small" :disabled="Boolean(edit && this.form.pictureList[0])" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">5M</div>
                </el-upload>
                <template v-if="this.form.pictureList[0]">
                    <div class="picture">
                        <div class="picture">
                            <img class="picture-max" :src="this.form.pictureList[0].url" alt="">
                        </div>
                        <div class="picture2">
                            <el-button class="picture-delete" type="danger" icon="el-icon-delete" circle @click="deletePicture"></el-button>
                        </div>
                    </div>
                </template>
            </el-form-item>
            <el-form-item v-else required label="">
                <template v-if="this.form.pictureList[0]">
                    <div class="picture">
                        <div class="picture">
                            <img class="picture-max" :src="this.form.pictureList[0].url" alt="">
                        </div>
                    </div>
                </template>
            </el-form-item>
            <titleInput :form="form" :edit="!edit"></titleInput>
            <el-form-item label="">
                <tagList v-if="edit" :labelList="this.form.tagList"></tagList>
                <ft-labels v-else :labels="this.form.tagList"></ft-labels>
            </el-form-item>
            <el-form-item v-if="edit">
                <el-button type="primary" @click="submit"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import tagList from '../components/tagList';
import titleInput from '../components/titleInput';

export default {
    name: 'pictureEdit',
    components: {
        tagList,
        titleInput
    },
    data() {
        return {
            title: '',
            edit: true,
            id: 0,
            form: this.createEmptyData()
        };
    },
    created() {
        this.checkPageStatus();
    },
    methods: {
        checkPageStatus() {
            if (this.$route.query.id) {
                // actioneditid
                //   
                this.edit = this.$route.query.action === 'edit';
                this.title = this.edit ? '' : '';
                this.id = this.$route.query.id;
                this.findDataById(this.id);
            } else {
                //  
                this.title = '';
                this.edit = true;
                this.id = 0;
                this.resetData();
            }
        },
        createEmptyData() {
            return {
                picture: '',
                pictureList: [],
                title: '',
                tagList: [],
            };
        },
        resetData() {
            this.form = this.createEmptyData();
        },
        findDataById(resourceId) {
            this.$loading({
                text: '...'
            });
            return this.$get('/resources/findResourcesById.json', { resourceId }).then((res) => {
                this.$loading().close();
                if (res.data.retcode === 200) {
                    const data = res.data.object;
                    this.form.title = data.resource.title;
                    this.form.pictureList = [{ url: data.resource.imageUrl }];
                    data.labels.forEach((value) => {
                        value.closable = true;
                    });
                    this.form.tagList = data.labels;
                } else {
                    this.form.pictureList = [];
                    this.$alert('');
                }
                return Promise.resolve(res);
            });
        },
        beforeUpload(file) {
            if (this.form.pictureList.length === 1) {
                return false;
            }
            if (!/(.jpg|.png|.jpeg)$/.test(file.name)) {
                this.$alert('jpg,pngjpeg');
                return false;
            }
            if (file.size > 1024 * 1024 * 5) {
                this.$alert(`5M${Number(file.size / (1024 * 1024)).toFixed(2)}M`);
                return false;
            }
            return true;
        },
        uploadSuccess(res) {
            if (res.retcode === 200 && res.imageUrl) {
                this.$message.success('');
                this.form.pictureList = [{ url: res.imageUrl }];
            } else {
                this.$alert('');
            }
        },
        uploadError() {
            this.$alert('');
        },
        deletePicture() {
            this.$confirm('').then(() => {
                this.form.pictureList = [];
            });
        },
        submit() {
            const params = {
                resourceType: 3,
                title: this.form.title,
                labels: this.form.tagList.map(label => label.labelId).join(','),
                imageUrl: this.form.pictureList[0] && this.form.pictureList[0].url
            };
            if (this.id) {
                params.resourceId = this.id;
            }
            if (params.title.length < 1) {
                this.$alert('');
                return;
            } if (params.title.length > 30) {
                this.$alert('30,');
                return;
            }
            if (!params.imageUrl) {
                this.$alert('');
                return;
            }
            this.$loading({
                text: '...'
            });
            this.$post('/resources/addOrModifyResource.json', params).then(() => {
                this.$loading().close();
                this.$alert('').then(() => {
                    this.back();
                });
            }).finally(() => {
                this.$loading().close();
            });
        },
        back() {
            this.$router.push('/marketing/list?type=picture');
        }
    }

};
</script>

<style scoped>
    .back-Btn{
        float: right;
    }
    .picture{
        display: flex;
        align-items: center;
        justify-content: start;
    }
    .picture2{
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .picture-delete{
        height: 40px;
        margin-left: 20px;
    }
    .picture-max{
        max-height: 300px;
        max-width: 500px;
    }
</style>
