<script>
import timeHandle from '../../utils/timeHandle.js';


const basicObjectNames = ['parentalStatus', 'gender'];
const healthObjectNames = ['rectusAbdominis', 'pelvicFloorMuscle', 'pelvicFloorMuscleScore', 'isWoundHealed', 'isExpansionBreast', 'campType', 'breastMilkLevel', 'slimmin', 'result', 'health'];
const basicQuestionMap = {
    gender: {
        F: '',
        M: ''
    },
    parentalStatus: {
        1: '',
        2: '',
    },
};
const healthQuestionMap = {
    rectusAbdominis: {
        1: '0-2',
        2: '2-4',
        3: '4',
        4: ''
    },
    pelvicFloorMuscle: {
        1: '',
        2: '',
        3: '1-2',
        4: ''
    },
    pelvicFloorMuscleScore: {
        1: '80',
        2: '60-80',
        3: '60',
        4: ''
    },
    isWoundHealed: {
        1: '',
        2: '',
        3: '',
        4: ''
    },
    isExpansionBreast: {
        1: '',
        2: ''
    },
    breastMilkLevel: {
        1: '',
        2: '<50%',
        3: '50%',
        4: ' 70%',
        5: ''
    },
    slimmin: {
        1: '',
        2: '',
        3: '',
        4: '//',
        5: '',
        6: ''
    },
    campType: {
        1: '',
        2: ''
    },
    result: {
        1: '',
        2: '',
        3: ''
    },
    health: {
        1: '',
        2: '(0-24)',
        3: ''
    }
};

export default {
    name: 'userInfo',
    props: [],
    data() {
        return {
            basicInfo: {}, // 
            healthInfo: {}, // 
            actionInfo: {} // 
        };
    },
    created() {
        const { userId } = this.$route.query;
        this.getBasicInfo(userId);
        this.getHealthInfo(userId);
        this.getActionInfo(userId);
    },
    methods: {
        getBasicInfo(userId) {
            this.$get('/statistic/user/userInfo.json', { userId }).then((res) => {
                const basicInfo = (res.data && res.data.object) || {};
                basicInfo.createTime = timeHandle.returnDate(basicInfo.createTime, 'hour');
                basicInfo.userBirthday = timeHandle.returnDate(basicInfo.userBirthday);
                basicInfo.babyBirthday = timeHandle.returnDate(basicInfo.babyBirthday);
                // basicInfo.gender = basicInfo.gender === 'F' ? '' : '';
                basicInfo.labels = basicInfo.labels || [];
                basicInfo.labels = basicInfo.labels.map((item) => {
                    let closable = false;
                    if (item.labelType === 1) {
                        closable = true;
                    }
                    return { ...item, closable };
                });

                basicObjectNames.forEach((item) => {
                    const infoTextArray = [];
                    const itemIndexArray = (`${basicInfo[item] || ''}`).split(',');
                    itemIndexArray.forEach((subItem) => {
                        const infoText = basicQuestionMap[item][subItem] || subItem;
                        infoTextArray.push(infoText);
                    });
                    basicInfo[item] = infoTextArray.join(',');
                });
                this.basicInfo = basicInfo;
            });
        },
        getHealthInfo(userId) {
            this.$get('/statistic/user/userHealthInfo.json', { userId }).then((res) => {
                const healthInfo = (res.data && res.data.object) || {};
                healthObjectNames.forEach((item) => {
                    const infoTextArray = [];
                    const itemIndexArray = (`${healthInfo[item] || ''}`).split(',');
                    itemIndexArray.forEach((subItem) => {
                        const infoText = healthQuestionMap[item][subItem] || subItem;
                        infoTextArray.push(infoText);
                    });
                    healthInfo[item] = infoTextArray.join(',');
                });
                this.healthInfo = healthInfo;
            });
        },
        getActionInfo(userId) {
            this.$get('/statistic/user/userActionInfo.json', { userId }).then((res) => {
                const actionInfo = (res.data && res.data.object) || {};
                actionInfo.isFollowFitcamp = this.isFollowWx(actionInfo.isFollowFitcamp);
                actionInfo.isFollowHotcamp = this.isFollowWx(actionInfo.isFollowHotcamp);
                actionInfo.isFollowFittime = this.isFollowWx(actionInfo.isFollowFittime);
                actionInfo.isFollowHealth = this.isFollowWx(actionInfo.isFollowHealth);
                actionInfo.isFollowFittimeShop = this.isFollowWx(actionInfo.isFollowFittimeShop);
                this.actionInfo = actionInfo;
            });
        },
        isFollowWx(val) {
            return val === 1 ? '' : '';
        },
        // 
        searchFunc(key) {
            const params = {
                labelPartition: 1, // 1
                labelGroupId: 0 // 0
            };
            if (key) {
                params.labelName = key;
            }
            return this.$get('/crm/label/search.json', params).then((res) => {
                const { object } = res.data;
                if (object && object.length) {
                    object.forEach((item) => {
                        item.closable = true;
                    });

                    return Promise.resolve(object);
                }

                return Promise.resolve([]);
            });
        },
        // 
        pickLabel(label = {}) {
            const { labels } = this.basicInfo;
            if (label.labelName && labels.indexOf(label) < 0 && this.basicInfo.userId) {
                this.$post('/crm/label/addRelation.json', { userId: this.basicInfo.userId, labelId: label.labelId, relationType: 1 }).then(() => {
                    labels.push(label);
                });
            }
        },
        // 
        deleteLabel(label = {}) {
            if (!label.labelId || !this.basicInfo.userId) return;
            this.$confirm(` ${label.labelName} ?`, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$loading('...');
                const { labels } = this.basicInfo;
                this.$get('/crm/label/relation/user/unbind.json', { labelId: label.labelId, userId: this.basicInfo.userId }).then(() => {
                    this.basicInfo.labels = labels.filter(item => item !== label);
                    this.$loading().close();
                });
            }).catch(() => {
                // 
            });
        },
        goBack() {
            this.$router.push('/crmUserList');
        }
    },
    mounted() {
    }
};
</script>

