<template>
  <div class="home">
    <div class="header">
      
      <div class="back-btn">
        <el-button @click="back()"
                   size="mini"></el-button>
      </div>
    </div>
    <div class="content">
      <div class="item">
        <div class="item-title">
          <span></span>
          <span class="font-blue"> {{categoryNum}}  {{totalNum}} </span>
        </div>
        <div class="item-content">
          <div class="list">
            <span class="list-name"></span>
            {{userRelationNum}}
          </div>
          <div class="list">
            <span class="list-name"></span>
            {{ingredientsRelationNum}}
          </div>
          <div class="list">
            <span class="list-name"></span>
            {{marketingRelationNum}}
          </div>
        </div>
      </div>
      <div class="item">
        <div class="item-title">
          <span></span>
        </div>
        <div class="item-content">
          <span></span>
          <el-select v-model="category"
                     size="small"
                     @change="categoryChange()">
            <el-option v-for="item in categoryList"
                       :key="item.value"
                       :label="item.label"
                       :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="item">
        <div class="btn-box">
          <el-button @click="search()"
                     type="success"
                     size="mini"></el-button>
          <br>
          <br>
          <el-button @click="clearSearch()"
                     type="danger"
                     size="mini"></el-button>
        </div>
        <div class="item-content"
             :class="category==='3' ? 'min-height' : ''">
          <div class="item-list"
               v-for="(item,index) in searchList"
               :key="index"
               :class="item.type==='time' || item.type==='input' ? 'item-block' : ''">
            <span class="search-name"
                  v-if="item.type !== 'input'">
              {{item.name}}
            </span>
            <div class="search-content"
                 :class="item.type==='time' ? 'search-time' : item.type==='input' ? 'search-input' : ''">
              <div v-if="item.type === 'select'">
                <el-select v-model="searchData[item.key]"
                           size="small">
                  <el-option v-for="(itemSelect, index2) in item.list"
                             :key="index2"
                             :label="itemSelect.name"
                             :value="itemSelect.value || itemSelect.id">
                  </el-option>
                </el-select>
              </div>
              <div v-if="item.type === 'integer' || item.type === 'decimal'">
                <div class="search-input">
                  <el-input size="small"
                            type="number"
                            placeholder=""
                            v-model="searchData[item.key+'Min']"></el-input>
                </div>
                
                <div class="search-input">
                  <el-input size="small"
                            type="number"
                            placeholder=""
                            v-model="searchData[item.key+'Max']"></el-input>
                </div>
                <span class="clear"
                      @click="clear(index)"></span>
              </div>
              <div v-if="item.type==='time'">
                <el-date-picker v-model="searchData[item.key+'Min']"
                                type="datetime"
                                size="small"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                placeholder="">
                </el-date-picker>
                
                <el-date-picker v-model="searchData[item.key+'Max']"
                                type="datetime"
                                size="small"
                                value-format="yyyy-MM-dd HH:mm:ss"
                                placeholder="">
                </el-date-picker>
                <span class="clear"
                      @click="clear(index)"></span>
              </div>
              <div v-if="item.type === 'input'">
                <el-input size="small"
                          v-model="searchData[item.key]"
                          :placeholder="item.placeholder"></el-input>
              </div>
            </div>
          </div>
          <div class="item-list tag-box">
            <div class="search-name">
              
            </div>
            <label-picker class="tag-chosen-field"
                          ref="tagList"
                          :searchFunc="searchFunc"
                          :labels="labels"
                          :auto-search="Boolean(true)"
                          :refresh-after-hide="Boolean(true)"
                          @pickLabel="pickLabel"
                          @deleteLabel="deleteLabel">
            </label-picker>
          </div>
          <div class="search-result"
               v-show="resultShow">
             {{searchResult.length}} 
            <el-button type="primary"
                       @click="connect()"
                       :disabled="!searchResult.length"
                       size="mini"></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 
import { foodList, userList, textList } from '@/utils/tagsSearch';
import labelPicker from '../../components/label-picker';

const R = require('ramda');

export default {
    name: 'addRelation',
    components: { labelPicker },
    props: ['id', 'type'],
    data() {
        return {
            originalSearchData: '',
            labelId: '',
            category: '',
            categoryList: [],
            searchList: [],
            foodList,
            userList,
            textList,
            searchData: {},
            resultShow: false,
            userRelationNum: 0, // 
            ingredientsRelationNum: 0, // 
            marketingRelationNum: 0, // 
            categoryNum: 0, // 
            totalNum: 0, // 
            labels: [], // 
            searchResult: [] // 
        };
    },
    created() {
    // id
        const tagType = this.$route.query.type;
        this.tagType = tagType;
        this.labelId = this.$route.query.id;
        // 
        if (tagType === '1') {
            this.categoryList = [{ label: '', value: '1' }];
            this.searchList = this.userList;
        }
        if (tagType === '2') {
            this.categoryList = [
                { label: '', value: '2' },
                { label: '', value: '3' }
            ];
            this.searchList = this.foodList;
        }
        if (tagType === '3') {
            this.categoryList = [
                { label: '', value: '1' },
                { label: '', value: '2' },
                { label: '', value: '3' }
            ];
            this.searchList = this.userList;
        }
        this.category = this.categoryList[0].value;
        this.resetSearchData(); // 
        this.getColorList(); // 
        this.getRegisterChannelsList(); // 
        this.getDetail(); // 
    },
    methods: {
        back() {
            // 
            this.$router.push(`/tag/detail?id=${this.labelId}`);
        },
        resetSearchData() {
            // searchData
            this.searchData = { labelIdList: '' };
            // searchData
            // $set()
            this.searchList.forEach((item) => {
                if (item.type === 'select') {
                    this.$set(
                        this.searchData,
                        item.key,
                        item.list.length ? item.list[0].value || '0' : '0'
                    );
                } else if (item.type === 'input') {
                    this.$set(this.searchData, item.key, '');
                } else {
                    this.$set(this.searchData, `${item.key}Min`, '');
                    this.$set(this.searchData, `${item.key}Max`, '');
                }
            });
            this.labels = [];
            this.originalSearchData = R.clone(this.searchData);
        },
        getDetail() {
            // 
            this.$get('/crm/label/detail.json', { labelId: this.labelId }).then(
                (res) => {
                    const data = res.data.object;
                    this.userRelationNum = data.userRelationNum;
                    this.ingredientsRelationNum = data.ingredientsRelationNum;
                    this.marketingRelationNum = data.marketingRelationNum;
                    this.categoryNum = (this.userRelationNum ? 1 : 0)
            + (this.ingredientsRelationNum ? 1 : 0)
            + (this.marketingRelationNum ? 1 : 0);
                    this.totalNum = this.userRelationNum
            + this.ingredientsRelationNum
            + this.marketingRelationNum;
                }
            );
        },
        getColorList() {
            // 
            this.$get('/crm/label/ingreIds/colors.json', { labelId: this.labelId })
                .then((res) => {
                    const data = res.data.object;
                    const list = [{ name: '', value: '0' }];
                    data.forEach((item) => {
                        list.push({
                            name: item,
                            value: item
                        });
                    });
                    this.foodList[3].list = list;
                })
                .catch(error => console.log(error));
        },
        getRegisterChannelsList() {
            // 
            this.$get('/crm/label/user/registerChannels.json', {
                labelId: this.labelId
            })
                .then((res) => {
                    this.userList[1].list = res.data.object;
                    // 
                    this.userList[1].list.unshift({
                        id: '0',
                        name: ''
                    });
                })
                .catch(error => console.log(error));
        },
        search() {
            // 
            // 
            const msg = this.checkData();
            if (msg) return this.$alert(msg);
            // 
            this.getList();
            return '';
        },
        checkData() {
            let msg = '';
            // 
            // 
            // 
            // 
            // 
            // 
            for (let i = this.searchList.length - 1; i >= 0; i--) {
                const item = this.searchList[i];
                // 
                const max = parseFloat(this.searchData[`${item.key}Max`]) || 0;
                // 
                const min = parseFloat(this.searchData[`${item.key}Min`]) || 0;
                if (item.type === 'integer') {
                    // 
                    if ((max && min && max < min) || min < 0 || max < 0) { msg = `${item.name}`; }
                    if (max && max % 1 !== 0) msg = `${item.name}`;
                    if (max && max < 0) msg = `${item.name}0`;
                    if (min && min % 1 !== 0) msg = `${item.name}`;
                    if (min && min < 0) msg = `${item.name}0`;
                }
                if (item.type === 'decimal') {
                    // 
                    if ((max && min && max < min) || min < 0 || max < 0) { msg = `${item.name}`; }
                    if (max) {
                        if (max < 0) {
                            msg = `${item.name}0`;
                        } else {
                            this.searchData[`${item.key}Max`] = Number(max).toFixed(1);
                        }
                    }
                    if (min) {
                        if (min < 0) {
                            msg = `${item.name}0`;
                        } else {
                            this.searchData[`${item.key}Min`] = Number(min).toFixed(1);
                        }
                    }
                }
                if (item.type === 'time') {
                    // 
                    const maxTime = new Date(this.searchData[`${item.key}Max`]).getTime();
                    const minTime = new Date(this.searchData[`${item.key}Min`]).getTime();
                    if (maxTime && minTime && maxTime < minTime) {
                        msg = `${item.name}`;
                    }
                }
                if (item.name === '') {
                    if (max && (max < 1 || max > 12)) { msg = `${item.name}1-12`; }
                    if (min && (min < 1 || min > 12)) { msg = `${item.name}1-12`; }
                }
            }
            return msg;
        },
        getList() {
            // 
            let url = '';
            if (this.category === '1') url = 'userIds';
            if (this.category === '2') url = 'ingreIds';
            if (this.category === '3') url = 'marketingIds';
            this.searchData.labelIdList = '';
            if (this.labels.length) {
                this.labels.forEach((item) => {
                    this.searchData.labelIdList += `${item.labelId},`;
                });
                this.searchData.labelIdList = this.searchData.labelIdList.substring(
                    0,
                    this.searchData.labelIdList.length - 1
                );
            }
            if (
                this.category === '1'
        && R.equals(this.searchData, this.originalSearchData)
            ) {
                this.$message.error('');
                return;
            }
            this.$loading('...');
            this.$get(`/crm/label/${url}.json`, this.searchData)
                .then((res) => {
                    this.searchResult = res.data.object || [];
                })
                .catch(error => console.log(error))
                .finally(() => this.$loading().close());
            this.resultShow = true;
        },
        clearSearch() {
            // 
            this.resetSearchData();
            this.resultShow = false;
        },
        categoryChange() {
            // 
            // 
            if (this.category === '1') this.searchList = this.userList;
            if (this.category === '2') this.searchList = this.foodList;
            if (this.category === '3') this.searchList = this.textList;
            this.$refs.tagList.search();
            this.resetSearchData();
        },
        clear(i) {
            // 
            this.searchData[`${this.searchList[i].key}Min`] = '';
            this.searchData[`${this.searchList[i].key}Max`] = '';
        },
        connect() {
            // 
            this.$confirm(
                '',
                '',
                {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }
            )
                .then(() => {
                    let relationIds = '';
                    this.searchResult.forEach((item) => {
                        relationIds += `${item},`;
                    });
                    relationIds = relationIds.substring(0, relationIds.length - 1);
                    let relationType = this.category; // relationType6
                    if (relationType === '3') {
                        relationType = '6';
                    }
                    const params = {
                        relationIds,
                        labelId: this.labelId,
                        relationType //  /(0) (1) (2) (4) (5) /(6) (7)
                    };
                    this.$loading('...');
                    this.$post('/crm/label/addRelation.json', params).then(() => {
                        this.$message.success('!');
                        this.resultShow = false;
                        // 
                        this.getDetail();
                        this.resetSearchData();
                    });
                })
                .catch(error => console.log(error))
                .finally(() => {
                    this.$loading().close();
                });
        },
        // 
        searchFunc(key) {
            // 1,23
            const labelPartitionMap = {
                1: '1,3', // 
                2: '2,3', // 
                3: '2,3' // 
            };
            const params = {
                labelType: 0, // 1,2, 0
                labelPartitions: labelPartitionMap[this.category] || '0', // map
                labelGroupId: 0 // 0
            };
            if (key) {
                params.labelName = key;
            }
            //  http://apidoc.rjft.net/project/110/interface/api/1138
            return this.$get('/crm/label/search.json', params).then((res) => {
                const { object } = res.data;
                if (object && object.length) {
                    object.forEach((item) => {
                        item.closable = true;
                    });

                    return Promise.resolve(object);
                }

                return Promise.resolve([]);
            });
        },
        // 
        pickLabel(label) {
            const index = this.labels.indexOf(label);
            if (index > -1) return this.$message.error('');
            this.labels.push(label);
            return '';
        },
        // 
        deleteLabel(label) {
            const index = this.labels.indexOf(label);
            if (index > -1) this.labels.splice(index, 1);
        }
    }
};
</script>

